import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  public skills: string = 'I am a software engineer, a robotics researcher and I work with different programming languages such as C/C++, Python, with ROS and JavaScript, TypeScript with Angular.\n'
    + 'SLAM, Computer Vision, Deep Learning and Mobile Robotics are my interests.';
  public text = '';
  public counter = 0;

  ngOnInit(): void {
    this.typeWriter();
  }

  public typeWriter() {
    if (this.counter < this.skills.length) {
      this.text += this.skills.charAt(this.counter);
      this.counter++;
      setTimeout(() => {
        this.typeWriter()
      }, 100);
    }
  }
}
